  vertical-align: baseline;
}
.sidenote-number:after {
  content: counter(sidenote-counter);
  font-size: 1rem;
  top: -0.5rem;
  left: 0.1rem;
}
.sidenote:before {
  content: counter(sidenote-counter) " ";
  top: -0.5rem;
}
